import { Component, h, Prop, Host } from "@stencil/core"
import { ButtonGroupLayout } from "./types"

@Component({
  tag: "sdx-button-group",
  styleUrl: "button-group.scss",
  shadow: true,
})
export class ButtonGroup {
  /**
   * Defines how buttons are aligned, sized and distributed.
   */
  @Prop() public layout: ButtonGroupLayout = "responsive"

  private getComponentClassNames() {
    return {
      component: true,
      [this.layout]: true,
    }
  }

  public render() {
    return (
      <Host class={this.layout}>
        <div class={this.getComponentClassNames()}>
          <slot />
        </div>
      </Host>
    )
  }
}
