$button-group-margin-v: $button-margin-vertical * 0.5;
$button-group-margin-h: $button-margin-horizontal * 0.5;

@mixin fill-behavior {
  width: 100%;
  min-width: 0;
}

:host {
  .component {
    display: flex;
    flex-wrap: wrap;

    // default
    flex-direction: row-reverse;

    margin: (-$button-group-margin-v) (-$button-group-margin-h);
  }

  ::slotted(sdx-button) {
    margin: $button-group-margin-v $button-group-margin-h;

    // when [layout="responsive"] but as it's the default, it's implicit
    @include bp-xs-max {
      width: 100%;
    }
  }

  .fixed,
  .fullwidth {
    flex-direction: row;
  }

  .fill {
    flex-wrap: nowrap;
  }

  .responsive-fill {
    @include bp-sm {
      flex-wrap: nowrap;
    }
  }
}

:host(.fill) {
  ::slotted(sdx-button) {
    @include fill-behavior;
  }
}

:host(.responsive-fill) {
  ::slotted(sdx-button) {
    @include bp-sm {
      @include fill-behavior;
    }
  }
}

:host(.fixed) {
  ::slotted(sdx-button) {
    @include bp-xs-max {
      width: auto;
    }
  }
}

:host(.fullwidth) {
  ::slotted(sdx-button) {
    width: 100%;
  }
}
